import Vue from 'vue'
import Router from 'vue-router'

import Left from '@/views/Left'
import Event from '@/views/Event'
import Recom from '@/views/Recom'
import ShopIn from '@/views/ShopIn'
import Banner from '@/views/Banner'
import Test from  '@/views/Test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/event/:id', 
      name: 'event',
      component: Event
    },
    { path: '/left', name: 'left', component: Left },
    { path: '/banner', name: 'left', component: Banner},
    { path: '/test', name: 'left', component: Test },
    { path: '/recom', name: 'recom', component: Recom },
    { path: '/shopIn', name: 'shopIn', component: ShopIn }
  ]
})



