    <img :src=pic(5)></img>
    <img :src=pic(6)></img>
    <img :src=pic(7)></img>
    <img :src=pic(8)></img>
    <img :src=pic(9)></img>
    <img :src=pic(10)></img>
    <img :src=pic(11)></img>
    <img :src=pic(12)></img>
    <img :src=pic(13)></img>
  </div>
</template>

<script>
export default {
  name: 'showIn',
  data(){
    return {
    } 
  },
  methods: {
    pic(id) {
      return "./static/instruct/" + id + ".jpg" 
    }
  }
}
</script>

<style scoped>
  img{
    display: block;
    width: 100vw;
    padding: 0px;
    margin: 0px;
  }
</style>




