<template>
  <div id="recomm">
    <img src="static/recom.png">

    <div id="products">

      <div v-for="p in products"  class="product">
          <img :src=time_type class="type_img">
          <img class="product_img" :src=p.image>
          <div class="product_desc">
            <div class="name">{{ p.productName }}</div>
            <div class="price">售价{{ p.salesPrice}}</div>
            <div class="market_price">市场价{{ p.referencePrice }}</div>
            <div class="buy_container">
            <button class="buy" 
                    @click=product_detail(p.id)>
                    立即购买</button>
          </div>
          </div>
      </div>
    
    </div>
  </div>
</template>

<script>
import Product_service from '../api/products.js';

const PRODUCT_ID = 6;

const time_type = "./static/v_type/time.png"
const qutity_type = "./static/v_type/qutity.png"

export default {
  name: 'recom',
  data(){
    return {
      time_type: time_type,
      qutity_type: qutity_type,
      products: []
    } 
  },
  methods: {
    get_products(id){
      Product_service.get_products(id, function(resp){
        that.products = resp.list; 
      })
    },
    product_detail(id){
      location.href = "http://mall.huishoubao.com.cn/#/project/goods/detail?id=" + id;
    }
  },
  created: function(){
    var that = this;
    //this.get_products(PRODUCT_ID)
    Product_service.get_products( 1, function(resp){
      that.products = resp.list; 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
      that.products.push(resp.list[0]); 
    })

  
  }
}
</script>

<style scoped>
  #recomm{
    min-height: 100vh; 
    background-color: #efefef;
    overflow: auto;
  }
  img{
    width: 100vw; 
  }
  #products{
  
  }
  .product{
    position: relative;
    border: 3px solid #333333; 
    margin: 8vw 10vw; 
    background-color: white;
  }
  .product:nth-child(even){
    border-bottom-right-radius: 25px; 
    -moz-border-bottom-right-radius: 25px; 
    -webkit-border-bottom-right-radius: 25px; 
    -o-border-bottom-right-radius: 25px; 
  }
  .product:nth-child(odd){
    border-top-left-radius: 25px; 
    -moz-border-top-left-radius: 25px; 
    -webkit-border-top-left-radius: 25px; 
    -o-border-top-left-radius: 25px; 
  }
  .product_img{
    width: 30%; 
    margin: 2% 9%;
    display: inline-block;
    vertical-align: middle;
  }
  .product_desc{
    width: 31%; 
    margin: 2% 7%;
    display: inline-block;
    padding: 0px;
    display: inline-block;
    vertical-align: middle;
  }
.name{
  text-align: left;
  font-size: 1rem;
  margin: .2rem 0rem;
}
.price{
  color: #E20A33;
  text-align: left;
  font-size: 1rem;
  margin: 4px 0px;
}
.market_price{
  text-align: left;
  text-decoration: line-through; 
  font-size: 10px;
  color: #777777;
  margin: .3rem 0rem;
}
.buy{
  color: white;
  background-color: #FF2952;
  margin: 2px auto;
  outline: none;
  font-size: 14px;
  border: 1px solid transparent;
  width: 100%;
  padding: .3rem .5rem;
  display: inline-block;
  border-radius: 2px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  -o-border-radius: 2px;
}
.type_img{
  position: absolute;
  width: 5vw;
  top: -0.8vw;
  margin-left: 72.9vw;
}

</style>




