<template>
  <div v-bind:class=current_class id="app" >
    <img :src=current_bg id="bg_img">
    <div id="products">

      <div v-for="p in products" style="display:inline-block;position:relative;">
        <img :src=type_pic class="type_img">
        <div class="product">
          <img :src=p.image class="product_img">
          <div class="name">{{ p.productName }}</div>
          <div class="price">售价{{ p.salesPrice}}</div>
          <div class="market_price">市场价{{ p.referencePrice }}</div>
          <div class="buy_container">
            <button class="buy" @click=product_detail(p.id)>立即购买</button>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import Product_service from '../api/products.js';

  const pic_1 = "./static/bg_1.jpg";
  const pic_2 = "./static/bg_2.jpg";
  const pic_3 = "./static/bg_3.jpg";
  const pic_4 = "./static/bg_4.jpg";
  const pic_5 = "./static/bg_5.png";

  const hot_pic = "./static/type/hot.png" 
  const group_pic = "./static/type/hot.png" 
  const limit_pic = "./static/type/hot.png" 
  const price_pic = "./static/type/hot.png" 

const types = [{ type_1: true} ,{ type_2: true},{ type_3: true},{ type_4: true}, {type_5: true}];
  export default {
  name: 'App',
  data(){
    return{
      pics: [pic_1, pic_2, pic_3, pic_4, pic_5],
      current_bg: pic_1,
      type_pic: hot_pic,
      products: [],
      current_class: types[0]
    } 
  },
  methods:{
    get_products(){
      Product_service.get_products(pic_num, function(resp){
        that.products = resp.list; 
      })
    },
    product_detail(id){
      location.href = "http://mall.huishoubao.com.cn/#/project/goods/detail?id=" + id;
    }
  },
  created:function() {
    var that = this;
    var id = parseInt(this.$route.params.id); 
    var pic_num = isNaN(id) ? 1 : id;
    this.current_bg = this.pics[pic_num-1]
    Product_service.get_products(pic_num, function(resp){
    //Product_service.get_products(pic_num, function(resp){
      that.products = resp.list; 
      //that.products.push(resp.list[0]); 
      //that.products.push(resp.list[0]); 
    })
    this.current_class= types[pic_num -1] 
  }
}
</script>

<style scoped>
.type_1{
  background-color: #8dccff;
}
.type_2{
  background-color: #f076a5;
}
.type_3{
  background-color: #8a8bff;
}
.type_4{
  background-color: #328cfe;
}
.type_5{
  background-color: #efefef;
}
#app{
  width: 100vw;
  overflow: auto;
}
#bg_img{
  position: absolute;
  width: 100vw;  
}
.product{
  position: relative;
  display: inline-block;
  width: 44vw;
  overflow: hidden;
  background-color: white;
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  -o-border-radius: 4px;
  margin: 0vw 1vw;
}
.product_img{
  display: inline-block;
  width: 30vw;
  margin: 1vw 7vw;
}
#products{
  margin-top: 110vw;
  padding: 0vw 4vw;
}
.name{
  text-align: center;
  font-size: 14;
}
.price{
  color: #E20A33;
  text-align: center;
  font-size: 18;
  margin: 4px 0px;
}
.market_price{
  text-align: center;
  text-decoration: line-through; 
  font-size: 10px;
  color: #777777;
}
.buy{
  color: white;
  background-color: #FF2952;
  margin: 2px auto;
  outline: none;
  border: 1px solid transparent;
  padding: 5px 25px;
  display: inline-block;
  border-radius: 2px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  -o-border-radius: 2px;
}
.buy_container{
  text-align: center;
  padding: 2px 0px 10px 0px;
}
.type_img{
  margin-top: -2vw;
  margin-left:34vw;
  width: 12vw;
  position: absolute;
  top: 8vw;
  z-index:100;
}
</style>



