import Config from "./config.js"

export default function(url, method, json_data, call_back){
  return fetch(Config.server_url + url, {
    method: method, 
    body: JSON.stringify(json_data),
    headers: {'Content-Type': 'application/json'}
  })
  .then(res => res.json())
  /*
  .then(function(resp){
    if(resp._data._ret != '0'){
      resp.status = false; 
    } 
  })
  */
  .then(resp => resp._data)
  .then(call_back)
  .catch(error => console.log("Error" + error ));
}



